.sitemapChecklist {
    position: relative;
    width: 99999px;
    font-size: 12px;
    padding: 100px;
    @include clearfix;
    font-family: "Trebuchet MS", Helvetica, sans-serif;
    
    @media print {
        a:after {
            content: '';
        }
        a[href]:after {
            content: none !important;
        }
        
    }
    
    ul {
        text-align: left;
        margin: 0;
        padding: 0;
        list-style: none;
        
        li {
            position: relative;
            display: block;
            box-shadow: 0 1px 0 0 $light-gray;
            padding-right: 12px;
            
            .icon-check_circle, .icon-cancel {
                top: 0.3em;
                position: absolute;
                right: 0;
            }
            
            > span {
                font-weight: bold;
            }
            
        }
        
    }
    
    &__title {
        font-weight: bold;
        line-height: 12px;
        margin-bottom: 5px;
        text-align: left;
    }
    
    > .sitemapChecklist__pagewrap {
        &:before {
            z-index: 1;
            content: '';
            height: $sitemapchecklist-linewidth;
            width: 100%;
            position: absolute;
            left: calc((#{$sitemapchecklist-boxwidth} / 2) + #{$sitemapchecklist-margin});
            top: -$sitemapchecklist-margin;
            background-color: $light-gray;
            display: block;
        }
        
        > .sitemapChecklist__container {
            &:before {
                z-index: 1;
                content: '';
                width: $sitemapchecklist-linewidth;
                height: ($sitemapchecklist-margin * 2);
                position: absolute;
                left: ($sitemapchecklist-boxwidth / 2);
                top: -(2 * $sitemapchecklist-margin);
                background-color: $light-gray;
                display: block;
            }
        }
        
        &:last-child {
            &:before {
                width: 0;
            }
        }
        
    }
    
    &__pagewrap {
        float: left;
        text-align: center;
        position: relative;
        
        &.has-children {
        
        }
        
        &.hidden {
            display: none;
        }
        
    }
    
    &__home {
        position: relative;
        text-align: center;
        width: 100%;
        float: left;
        margin-bottom: (2 * $sitemapchecklist-margin);
        &:before {
            z-index: 1;
            content: '';
            width: $sitemapchecklist-linewidth;
            height: ($sitemapchecklist-margin * 2);
            position: absolute;
            left: ($sitemapchecklist-boxwidth / 2) + $sitemapchecklist-margin;
            bottom: -($sitemapchecklist-margin);
            background-color: $light-gray;
            display: block;
        }
    }
    
    &__children {
        position: relative;
        clear: left;
        @include clearfix;
        
        .sitemapChecklist__pagewrap {
            clear: left;
            margin-left: ($sitemapchecklist-boxwidth / 2) + (2 * $sitemapchecklist-margin);
            
            .sitemapChecklist__container {
                &:before {
                    z-index: 1;
                    content: '';
                    height: $sitemapchecklist-linewidth;
                    width: $sitemapchecklist-margin * 2;
                    position: absolute;
                    left: -($sitemapchecklist-margin * 2);
                    bottom: ($sitemapchecklist-boxheight / 2);
                    background-color: $light-gray;
                    display: block;
                }
            }
            
        }
        
        &:before {
            z-index: 1;
            content: '';
            width: $sitemapchecklist-linewidth;
            height: 100%;
            position: absolute;
            left: calc((#{$sitemapchecklist-boxwidth} / 2) + #{$sitemapchecklist-margin});
            bottom: ($sitemapchecklist-boxheight / 2) + $sitemapchecklist-margin;
            background-color: $light-gray;
            display: block;
        }
        
        &__toggle {
            position: absolute;
            top: -($sitemapchecklist-buttonsize - $sitemapchecklist-margin);
            left: ($sitemapchecklist-boxwidth/2)-5;
            width: $sitemapchecklist-buttonsize;
            height: $sitemapchecklist-buttonsize;
            z-index: 4;
            &:before {
                content: '';
                background-image: url(data:image/svg+xml;utf8;base64,);
                width: 20px;
                height: 20px;
                display: block;
                background-size: contain;
                
            }
            
            &.collapsed {
                &:before {
                    content: '';
                    background-image: url(data:image/svg+xml;utf8;base64,);
                    width: 20px;
                    height: 20px;
                    display: block;
                    background-size: contain;
                    
                }
            }
            
        }
        
    }
    
    &__container {
        padding: 3px 10px 10px 10px;
        z-index: 2;
        position: relative;
        float: left;
        width: $sitemapchecklist-boxwidth;
        height: $sitemapchecklist-boxheight;
        background-color: $color-white;
        box-shadow: 0 0 6px 2px $light-gray;
        margin: $sitemapchecklist-margin;
        
    }
    
    &__details {
        
        .status-ok, .status-fail {
            width: 12px;
            height: 100%;
            display: inline-block;
            background-size: 12px;
            position: absolute;
            top: 0;
            right: 0;
            background-repeat: no-repeat;
            background-position: 50%;
        }
        
        .status-ok {
            background-image: url(data:image/svg+xml;utf8;base64,)
        }
        .status-fail {
            background-image: url(data:image/svg+xml;utf8;base64,)
        }
    }
    &__edit {
        width: 20px;
        height: 20px;
        display: inline-block;
        background-size: 12px;
        position: absolute;
        bottom: -5px;
        right: -5px;
        background-repeat: no-repeat;
        background-position: 50%;
        background-image: url('data:image/svg+xml;base64,');
        background-color: #f7de2d;
        border-radius: 100%;
        z-index: 99;
        box-shadow: 0 0 3px rgba(0, 0, 0, 0.59);
        transition: transform 200ms ease;
        &:hover{
            transition: transform 200ms ease;
            transform: rotate(-40deg);
        }
    }
}
